"use client";

import React from "react";

const DeliveryAddressCard = () => {
    return (
        <section className="bg-white rounded-3xl p-8 shadow-sm flex flex-col items-center justify-center text-center h-full min-h-[250px]">
            <p className="text-xl font-bold text-gray-900 mb-6 max-w-[200px]">
                You don’t have any delivery address
            </p>

            <button className="bg-[#003625] text-white px-8 py-3 rounded-full text-sm font-medium hover:bg-opacity-90 transition-all cursor-pointer">
                Add Address
            </button>
        </section>
    );
};

export default DeliveryAddressCard;
